footer{
 padding-top: 5rem;
 
}

.footer-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 30vh;
    margin-top: 80px;
    margin-bottom: 80px;
    text-align: center;
  }
  
  .container.footer-container h1 {
   
    font-weight: 100;
    font-size: 25px;
    margin: 0 auto;
    text-align: center;
    text-transform: capitalize;
  }
  
  .container.footer-container h2 {
    font-weight: 100;
    font-size: 20px;
    margin-top: 15px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    padding: 10px 20px;
    border-radius: 5px;
  }
  .container.footer-container h2:hover {
    background-color: rgba(0, 0, 0, 0.068);
  }
  
  .social-links {
    padding: 10px;
    margin: 20px auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .social-links .social {
    height: 45px;
    width: 45px;
    background-color: rgba(0, 0, 0, 0.548);
    border-radius: 0.5rem;
    margin: 0 5px;
    padding: 10px;
    backdrop-filter: blur(10px);
    cursor: pointer;
    transition: background 0.2s ease;
  }
  
  .social-links .social:hover {
    background-color: rgba(255, 255, 255, 0.336);
    fill: var(--color-pri);
  }
  
  .footer_copy{
margin-bottom: 3rem;
  }

  @media screen and (max-width: 1024px) {
    .footer_copy{
      margin-bottom: 6rem;
        }
  }